import React from 'react';
import { NavLink } from 'react-router-dom';
import style from './Friend.module.scss';
import { Avatar } from 'antd';

const Friend = ({name,id}) => {
    return (
        <NavLink to={`/profile/${id}`} className={style.friend} activeClassName={style.selectFriend}>
            <Avatar src="data:image/png;base64," size={48} />
            <div className={style.friendName}>{name}</div>
        </NavLink>
    )
}

export default Friend;